//PROJECTS PAGE//
.projects_page{
  display: flex;
  width: 100%;
  min-height: 100vh;
  //border: 1px solid green;

}

@media (max-width: 570px){
    .projects_page{
      flex-wrap: wrap;
    }
}


.work-component-individual{
  //border: 1px solid blue;
  flex-order: 1;
  flex-grow: 1;
  flex-shrink: 1;
  flex-basis: 400px;
  background: #fb5353;
  
}




.work-component-individual__image{
  width: 100%;
  height: auto;
  mix-blend-mode: screen;
}


.work-component-individual__description{
  width: 100%;
}



.work-component-individual__description__paragraph{
  padding: 0.5em 1.5em 0em 1.5em;
  font-size: 1.05em;
  line-height: 1.6em;
  font-weight: 300;
  color: #3b3633;
}

.work-component-individual__description__dates{
  padding: 0em 1.5em 0em 1.5em;
  font-size: 1.05em;
  font-weight: 500;
  color: #3b3633;
}

.project-section{
  flex-order: 2;
  flex-grow: 2;
  flex-shrink: 2.3;
  flex-basis: 930px;
}



